import React, { SyntheticEvent } from 'react';
import FirebaseInstance, { Firebase } from '../../lib/Firebase/firebase';
import { Redirect } from 'react-router-dom';

export class SignUpComponent extends React.Component<{firebase:Firebase}> {

    state:any = {
        formValue:{},
        redirect: false
    };

    constructor(props:any){
        super(props);
        this.setValue = this.setValue.bind(this);
        this.submit = this.submit.bind(this);
        this.load = this.load.bind(this);
    }


    setValue(event:any){
        const id = (event.currentTarget).getAttribute('custom-id');
        const value = event.currentTarget['value'];
        this.setState(function(state:any){
            state.formValue[id] = value;
            return state;
        });
    }

    submit(event:SyntheticEvent){
        event.preventDefault();
        if(this.state.formValue['password'] === this.state.formValue['password']){
            FirebaseInstance.createUserWithEmailAndPassword(this.state.formValue['email'], this.state.formValue['password'])
            .then((data)=>{
                window.alert("Su cuenta ha sido creada satisfactoriamente.");
                this.setState({
                    redirect: true
                });
            })
            .catch(function(err){
                window.alert("Hubo un error creando tu cuenta. Por favor, verifique sus datos e intente nuevamente.");
            })
        }
    }

    async load(){
        await FirebaseInstance.waitForAuth();
        if(FirebaseInstance.auth.currentUser){
            this.setState({
                redirect: true
            });
        }
    }

    componentDidMount(){
        this.load();
    }

    render(){
        if(this.state.redirect){
            return (
                <Redirect to="/"/>
            );
        }
        return (
            <div>
                <form style={{padding:'2em'}} onSubmit={this.submit}>
                    <h3>Registrarte</h3>
                    <fieldset className="uk-fieldset">

                        <legend className="uk-legend" style={{fontSize:'1.2em'}}>Email</legend>

                        <div className="uk-margin">
                            <input onChange={this.setValue} custom-id='email' className="uk-input" type="email" placeholder="Email"/>
                        </div>

                        <legend className="uk-legend" style={{fontSize:'1.2em'}}>Contrasea</legend>

                        <div className="uk-margin">
                            <input onChange={this.setValue} custom-id='password' className="uk-input" type="password" placeholder="Password"/>
                        </div>

                        <legend className="uk-legend" style={{fontSize:'1.2em'}}>Repetir Contrasea</legend>

                        <div className="uk-margin">
                            <input onChange={this.setValue} custom-id='repeat-password' className="uk-input" type="password" placeholder="Confirm"/>
                        </div>

                        <button className="uk-button uk-button-primary" role="submit">Crear Cuenta</button>

                    </fieldset>
                </form>

            </div>
        );
    }
}