import { IOfferRating } from "./interface/Offer";
import Firebase from "./Firebase";
import { IBidRating } from "./interface/Bid";
import BidManager from "./Bid";
import OfferManager from "./Offer";

export class RateManager {
    private static _instance:RateManager;
    private constructor(){}

    public static get Instance(){
        this._instance = this._instance ? this._instance : new RateManager();
        return this._instance;
    }

    async rateBid(comment:string, rate:string, bidId:string){
        if(!Firebase.auth.currentUser){
            throw new Error("User must be logged in to rate");
        }
        if(!comment || !rate || !bidId){
            throw new Error("Invalid data for offer");
        }
        let bidPoster = await BidManager.getBid(bidId);
        let offerCreationPayload:IBidRating = {
            comment, rate: parseInt(rate), bidId, poster: Firebase.auth.currentUser?.uid,
            ratedUser: bidPoster.poster+"",
            createdAt: Firebase.firestoreTimestamp,
            offerId: null
        };
        let existing = await this.hasRateOnBid(bidId);
        if(existing){
           throw new Error("Can't add more than one review for a bid."); 
        }
        let rating = await Firebase.firestore.collection(`rating`).add(offerCreationPayload);
        return rating.id;

    }

    async rateOffer(comment:string, rate:string, offerId:string){
        if(!Firebase.auth.currentUser){
            throw new Error("User must be logged in to rate");
        }
        if(!comment || !rate || !offerId){
            throw new Error("Invalid data for offer");
        }
        let offer = await OfferManager.getOffer(offerId);
        let offerCreationPayload:IOfferRating = {
            comment, rate: parseInt(rate), offerId, poster: Firebase.auth.currentUser?.uid,
            ratedUser: offer.poster+"",
            createdAt: Firebase.firestoreTimestamp,
            bidId: null
        };
        let existing = await this.hasRateOnOffer(offerId);
        if(existing){
           throw new Error("Can't add more than one review for an offer."); 
        }
        let rating = await Firebase.firestore.collection(`rating`).add(offerCreationPayload);
        return rating.id;
    }

    async hasRateOnOffer(offerId:string){
        if(!Firebase.auth.currentUser){
            return false;
        }
        let existing = await Firebase.firestore.collection(`rating`)
        .where("poster", "==", Firebase.auth.currentUser.uid)
        .where("offerId", "==", offerId)
        .limit(1).get();
        return !existing.empty;
    }

    async fetchOfferRatings(offerId:string, startAfter?:Date, stopBefore?:Date, limit?:number, fromUser?:string){

        let query = Firebase.firestore.collection('rating')
        .where("offerId", "==", offerId);

        query = <any>query.orderBy('createdAt', 'asc');
        if(startAfter){
            query = <any>query.startAfter(startAfter);
            if(limit){
                query = <any>query.limit(limit);
            }
        }else if(stopBefore){
            query = <any>query.endBefore(stopBefore);
            if(limit){
                query = <any>query.limitToLast(limit);
            }
        }else{
            if(limit){
                query = <any>query.limit(limit);
            }
        }
        let offers = await query.get();
        let offersToReturn:IOfferRating[] = offers.docs.map(ratingEl => {
            let offer:IOfferRating = <IOfferRating>ratingEl.data();
            offer.createdAt = offer.createdAt.toDate();
            offer.id = ratingEl.id;
            return offer;
        });
        return offersToReturn;
    }

    /**
     * 
     * @param ratedUser The user that was rated by this rating
     * @param ratingType Either offer or bid
     * @param startAfter A date in time to start counting
     * @param stopBefore A date in time to stop counting
     * @param limit The limit of the records to get
     */
    async fetchUserRatings(ratedUser:string, ratingType:'offer'|'bid', startAfter?:Date, stopBefore?:Date, limit?:number){
        let query = Firebase.firestore
            .collection('rating')
            .where("ratedUser", "==", ratedUser);

        if(ratingType === 'offer'){
            query = query.where('bidId', '==', null);
        }else if(ratingType === 'bid'){
            query = query.where('offerId', '==', null);
        }

        query = <any>query.orderBy('createdAt', 'asc');
        if(startAfter){
            query = <any>query.startAfter(startAfter);
            if(limit){
                query = <any>query.limit(limit);
            }
        }else if(stopBefore){
            query = <any>query.endBefore(stopBefore);
            if(limit){
                query = <any>query.limitToLast(limit);
            }
        }else{
            if(limit){
                query = <any>query.limit(limit);
            }
        }
        let offers = await query.get();
        return <IOfferRating[]> offers.docs.map(ratingEl => {
            let offer:IOfferRating = <IOfferRating>ratingEl.data();
            offer.createdAt = offer.createdAt.toDate();
            offer.id = ratingEl.id;
            return offer;
        });
    }

    async hasRateOnBid(bidId:string){
        if(!Firebase.auth.currentUser){
            return false;
        }
        let existing = await Firebase.firestore.collection(`rating`)
        .where("poster", "==", Firebase.auth.currentUser.uid)
        .where("bidId", "==", bidId)
        .limit(1).get();
        return !existing.empty;
    }

}

export default RateManager.Instance;