import { IOffer } from "./interface/Offer";
import CheckManager from "./Checker";
import Firebase from "./Firebase/firebase";
import { OFFER_STATUS, BID_STATUS } from "../constants/status";
import { IBidCreationPayload } from "./interface/Bid";
import { IContactUpdatePayload } from "./interface/Contact";

export class BidManager {

    private static _instance: BidManager ;

    private constructor(){

    }

    public static get Instance(){
        this._instance = this._instance ? this._instance : new BidManager ();
        return this._instance;
    }


    async createBid(bidData:IBidCreationPayload){
        let newBidId = Firebase.firestore.collection('bid').doc().id;
        if(!Firebase.auth.currentUser){
            throw new Error("User must be logged in to enable this action");
        }
        bidData.poster = Firebase.auth.currentUser.uid
        if(!CheckManager.hasValidBidData(bidData)){
            throw new Error("Invalid bid data");
        }
        let existing = await Firebase.firestore.collection('bid')
        .where('poster', '==', bidData.poster)
        .where('offer', '==', bidData.offer)
        .get();
        if(existing.size > 0){
            throw new Error("Can't bid two times on the same offer");
        }

        bidData.status = BID_STATUS.ACTIVE;
        bidData.createdAt = Firebase.firestoreTimestamp;
        await Firebase.firestore.collection("bid").doc(newBidId).set(bidData);
        return newBidId;
    }

    async hasBid(offerId:string){
        if(!Firebase.auth.currentUser){
            return false;
        }
        let res = await this.getBids(offerId);
        return res.size > 0;
    }

    async getBidContact(userId:string):Promise<IContactUpdatePayload>{
        let doc = await Firebase.firestore.collection('contact').doc(userId).get();
        return <any>doc.data();
    }

    async getBid(bidId:string):Promise<IBidCreationPayload>{
        let bidDoc = await Firebase.firestore.collection('bid').doc(bidId).get();
        let bid:IBidCreationPayload = {...<any>bidDoc.data(), id: bidDoc.id};
        return bid;
    }

    //Get the bids from an offer and an user
    async getBids(offerId:string):Promise<firebase.firestore.QuerySnapshot<firebase.firestore.DocumentData>>{
        if(!Firebase.auth.currentUser){
            throw new Error('No puedes obtener tus pujas si no has accedido a tu cuenta')
        }
        let existing = await Firebase.firestore.collection('bid')
        .where('poster', '==', Firebase.auth.currentUser.uid)
        .where("offer", "==", offerId)
        .get();
        return existing;
    }

    async getGeneralBids(offerId:string):Promise<firebase.firestore.QuerySnapshot<firebase.firestore.DocumentData>>{
        let existing = await Firebase.firestore.collection('bid')
        .where("offer", "==", offerId)
        .get();
        return existing;
    }

    async fetchGeneralBids(offerId:string, startAfter?:Date, stopBefore?:Date, limit?:number){
        let query = Firebase.firestore.collection('bid')
        .where("offer", "==", offerId);

        query = <any>query.orderBy('createdAt', 'asc');
        if(startAfter){
            query = <any>query.startAfter(startAfter);
            if(limit){
                query = <any>query.limit(limit);
            }
        }else if(stopBefore){
            query = <any>query.endBefore(stopBefore);
            if(limit){
                query = <any>query.limitToLast(limit);
            }
        }else{
            if(limit){
                query = <any>query.limit(limit);
            }
        }
        let offers = await query.get();
        let offersToReturn:IBidCreationPayload[] = [];
        
        offers.docs.forEach((offerEl)=>{
            let offer:IBidCreationPayload = <IBidCreationPayload>offerEl.data();
            offer.createdAt = offer.createdAt.toDate();
            offer.id = offerEl.id;
            offersToReturn.push(offer);
        });

        return offersToReturn;
    }

    async hasGeneralBids(offerId:string):Promise<boolean>{
        let existing = await Firebase.firestore.collection('bid')
        .where("offer", "==", offerId)
        .limit(1)
        .get();
        return !existing.empty;
    }

    async cancelBid(offerId:string){
        let bids = await this.getBids(offerId);
        if(bids.size>0){
            for(const bid of bids.docs){
                try{
                    await bid.ref.update({
                        status: BID_STATUS.CANCELLED
                    })
                }catch(e){}
            }
        }
    }

    async acceptBid(bidId:string){
        try{
            await Firebase.firestore.doc(`bid/${bidId}`).update({
                status: BID_STATUS.ACCEPTED
            });
        }catch(e){
            throw new Error("Error aceptado la puja");
        }
    }

    async rejectBid(bidId:string){
        try{
            await Firebase.firestore.doc(`bid/${bidId}`).update({
                status: BID_STATUS.REJECTED
            });
        }catch(e){
            throw new Error("Error rechazando la puja");
        }
    }


}

export default BidManager .Instance;