import React, { SyntheticEvent } from 'react';
import OfferManager from '../../lib/Offer';
import UtilsManager from '../../lib/Utils';
import { IOffer } from '../../lib/interface/Offer';
import Firebase from '../../lib/Firebase';
import {Link} from 'react-router-dom';
import { IBidCreationPayload } from '../../lib/interface/Bid';
import BidManager from '../../lib/Bid';
import { BID_STATUS, OFFER_STATUS } from '../../constants/status';
import BidListing from '../BidListing';
import { IImage } from '../../lib/interface/Media';
import RateSnippet from '../RateSnippet';
import RateManager from '../../lib/Rate';
import RateListing from '../RateListing';

declare const UIkit:any;
export class ViewOwnOfferComponent extends React.Component<{match:{params:{postId:string}}}> {

    state:any = {
        formValue:{},
        b64Image: "",
        hasBids: false
    };

    constructor(props:any){
        super(props);
        this.setValue = this.setValue.bind(this);
        this.submit = this.submit.bind(this);
        this.uploadNewImage = this.uploadNewImage.bind(this);
        this.load = this.load.bind(this);
        this.closeOffer = this.closeOffer.bind(this);
    }


    setValue(event:any){
        const id = (event.currentTarget).getAttribute('custom-id');
        const value = event.currentTarget['value'];
        this.setState(function(state:any){
            state.formValue[id] = value;
            return state;
        });
    }

    submit(event:SyntheticEvent){
        event.preventDefault();
        OfferManager.updateOffer(this.state.formValue, this.postId)
        .then(function(data){
            window.alert("Se ha guardado su oferta satisfactoriamente");
        })
        .catch(function(err){
            window.alert("Error actualizando la oferta");
        })
    }

    get exchangeRate(){
        let offeringAmmount = this.state.formValue['offer_ammount'] || 1;
        let acceptingAmmount = this.state.formValue['accept_ammount'] || 1;
        return offeringAmmount / acceptingAmmount;
    }

    get postId(){
        return this.props.match.params.postId;
    }

    async loadPost(){
        if(this.state.loadingPost){
            return;
        }
        this.setState({
            loadingPost: true
        });
        try{
            let data = await OfferManager.fetchOffer(this.postId)
            if(!data){
                throw new Error("Offer not found");
            }
            let hasBids = await BidManager.hasGeneralBids(this.postId);
            this.setState(function(oldState:any){
                oldState['formValue'] = data;
                oldState['hasBids'] = hasBids;
                return oldState;
            });
        }catch(e){
            window.alert("Error fetching offer");
        }
        this.setState({
            loadingPost: false
        });
    }

    newImageInput(){
        document.getElementById('newImageInput')?.click();
    }

    async load(){
        if(this.postId){
            await this.loadPost();
        }
        
        let element:any = document.getElementById("newImageInput");
        element.onchange = (event:any)=>{
            let input = event.target;
            if (input.files && input.files[0]) {
                var reader = new FileReader();
                
                reader.onload = (e:any) => {
                    //$('#blah').attr('src', e.target.result);
                    console.log("Did read the image as b64");
                    this.setState({
                        b64Image: e.target.result,
                        newImageFile: input.files[0]
                    });
                }
                reader.readAsDataURL(input.files[0]); // convert to base64 string
            }
        }
    }

    componentDidMount(){
        this.load()
        .then(function(){

        })
        .catch(function(e){
            console.log({e});
            window.alert("Error loading offer");
        });
    }

    async uploadNewImage(){

        if(this.state.uploading){
            return;
        }
        this.setState({
            uploading: true
        });
        try{
            await OfferManager.uploadImage(this.postId, this.state.newImageFile)
            this.setState({
                b64Image: null,
                newImageFile: null
            });
            this.loadPost();
        }catch(e){
            console.log({e});
            window.alert("Error uploading image");
        }

        this.setState({
            uploading: false
        });
    }

    async closeOffer(){
        if(this.state.closing){
            return;
        }
        this.setState({
            closing: true
        });
        try{
            await OfferManager.closeOffer(this.postId);
            await this.loadPost();
            window.alert("Offer has been closed successfully");
        }catch(e){
            console.log({e});
            window.alert("Error cancelling offer");
        }
        this.setState({
            closing: false
        });
        
    }

    get canChooseImage(){
        return this.canSave && !this.loading;
    }

    get loading(){
        return this.state.uploading || this.state.closing || this.state.saving || this.state.loadingPost;
    }

    get canSave(){
        return (this.state.formValue.status === OFFER_STATUS.ACTIVE) && this.isOwn && !this.loading && !this.state.hasBids;
    }


    get isOwn(){
        return Firebase.auth.currentUser && this.state.formValue.poster && Firebase.auth.currentUser.uid === this.state.formValue.poster;
    }

    async removeImage(image:IImage){
        try{
            if(window.confirm("Do you want to remove this image?")){
                await OfferManager.removeImage(this.postId, image);
                await this.loadPost();
                window.alert("Image successfully removed");
            }
        }catch(e){
            window.alert("Error removing the image");
        }
    }

    render(){
        return (
            <div>
                <form style={{padding:'2em'}} onSubmit={this.submit}>
                    <h3>Oferta</h3>
                    <fieldset className="uk-fieldset">

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Ttulo</label>
                            <div className="uk-form-controls">
                                <input disabled={!this.canSave} value={this.state.formValue['title']} onChange={this.setValue} custom-id='title' className="uk-input" type="text" placeholder="Title"/>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Descripcin de La Oferta</label>
                            <div className="uk-form-controls">
                                <textarea disabled={!this.canSave} value={this.state.formValue['description']} onChange={this.setValue} custom-id='description' className="uk-textarea"></textarea>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Moneda de Oferta</label>
                            <div className="uk-form-controls">
                                <select disabled={!this.canSave} value={this.state.formValue['offer_currency']} className="uk-select" id="form-stacked-select" onChange={this.setValue} custom-id='offer_currency'>
                                    <option>Selecciona tu moneda</option>
                                    <option value="USD">USD</option>
                                    <option value="BSF">BSF</option>
                                </select>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Cantidad en Oferta</label>
                            <div className="uk-form-controls">
                                <input disabled={!this.canSave} value={this.state.formValue['offer_ammount']} onChange={this.setValue} custom-id="offer_ammount" type="number" className="uk-input"></input>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Tipo de Pago</label>
                            <div className="uk-form-controls">
                                <select disabled={!this.canSave} value={this.state.formValue['offer_payment_method']} onChange={this.setValue} custom-id="offer_payment_method" className="uk-select" id="form-stacked-select">
                                    <option>Selecciona tu Mtodo de Pago</option>
                                    <option value="transferencia">Transferencia</option>
                                    <option value="efectivo">Efectivo</option>
                                </select>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Acepta Moneda</label>
                            <div className="uk-form-controls">
                                <select disabled={!this.canSave} value={this.state.formValue['accept_currency']} onChange={this.setValue} custom-id="accept_currency" className="uk-select" id="form-stacked-select">
                                    <option>Seleccione la moneda a aceptar</option>
                                    <option value="USD">USD</option>
                                    <option value="BSF">BSF</option>
                                </select>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Cantidad a Aceptar</label>
                            <div className="uk-form-controls">
                                <input disabled={!this.canSave} value={this.state.formValue['accept_ammount']} onChange={this.setValue} custom-id="accept_ammount" type="number" className="uk-input"></input>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Acepta Mtodo de Pago</label>
                            <div className="uk-form-controls">
                                <select disabled={!this.canSave} value={this.state.formValue['accept_payment_method']} onChange={this.setValue} custom-id="accept_payment_method" className="uk-select" id="form-stacked-select">
                                    <option>Seleccione su Mtodo de Pago</option>
                                    <option value="transferencia">Transferencia</option>
                                    <option value="efectivo">Efectivo</option>
                                </select>
                            </div>
                        </div>

                        <div className="uk-position-relative uk-visible-toggle uk-light" custom-tabindex="-1" uk-slider={""}>
                            <ul className="uk-slider-items uk-child-width-1-2 uk-child-width-1-3@s uk-child-width-1-4@m">

                                <input type="file" hidden={true} id="newImageInput"></input>
                                {
                                    this.canChooseImage ?
                                        !this.state.b64Image ? 
                                            <li style={{maxWidth:'400px', maxHeight:'300px', display:'inline-block',textAlign:'center'}}>
                                                <button className="uk-button" disabled={!this.canChooseImage} style={{backgroundColor:'#1e87f0', color:'white'}} onClick={this.newImageInput}>Selecciona nueva Imgen</button>
                                            </li> :
                                        <>
                                            <li style={{maxWidth:'400px', maxHeight:'300px', display:'inline-block'}}>
                                                <img data-src={this.state.b64Image} alt="Bill Image" uk-img={""}></img>
                                                <button className="uk-button" disabled={!this.canChooseImage} style={{backgroundColor:'#1e87f0', color:'white'}} onClick={this.uploadNewImage}>Sube Nueva Imgen</button>
                                            </li>
                                        </> :
                                    <></>
                                }
                                {
                                    this.state.uploading ?
                                    <li>
                                        <h3 style={{color:'black'}}>Subiendo imgen...</h3>
                                    </li> : <></>
                                }
                                {
                                    this.state.formValue && this.state.formValue.images ? 
                                    this.state.formValue.images.map((element:IImage) => 
                                        <li key={element.id}>
                                            <img src={element.downloadURL} style={{width:'300px',maxHeight:'500px'}}/>
                                            {
                                                this.canChooseImage ? 
                                                <button className="uk-button" disabled={!this.canChooseImage} style={{backgroundColor:'#1e87f0', color:'white'}} onClick={()=>this.removeImage(element)}>Eliminar Imgen</button>
                                                : <></>
                                            }
                                            
                                        </li>
                                    ) : <></>
                                }
                            </ul>

                            <a className="uk-position-center-left uk-position-small uk-hidden-hover" uk-slidenav-previous={""} uk-slider-item={"previous"}></a>
                            <a className="uk-position-center-right uk-position-small uk-hidden-hover" uk-slidenav-next={""} uk-slider-item={"next"}></a>
                        </div>

                        <div className="uk-margin">
                            <legend className="uk-legend" style={{fontSize:'1.2em'}}>Tasa de Cambio: <b>{this.exchangeRate}</b></legend>
                        </div>
                        
                        {
                            this.canSave ? 
                            <button className="uk-button uk-button-primary" role="submit">Guardar Oferta</button>
                            : this.state.hasBids ? 
                                <h3>Esta oferta tiene pujas. No puede ser actualizada.</h3>
                            : <></>
                        }
                        {
                            this.state.formValue.status == OFFER_STATUS.ACTIVE ? 
                            <>
                                <button className="uk-button uk-button-primary" onClick={this.closeOffer}>Cerrar Oferta</button>
                            </> : <>
                            <h3>This offer has been <b>{OfferManager.getOfferStatus(this.state.formValue.status)}</b></h3>
                            </>
                        }
                        
                    </fieldset>
                </form>
            </div>
        );
    }
}

export class ViewOfferComponent extends React.Component<{match:{params:{postId:string}}}> {

    state:{
        postData: IOffer,
        bid: IBidCreationPayload | null,
        loading:{
            bid: boolean,
            post: boolean,
            bidAction: boolean
        },
        hasBids: boolean,
        ratedOffer: boolean
    } = {
        postData: {
            accept_ammount: 1,
            accept_currency: "BSF",
            description: "1",
            offer_ammount: 1,
            offer_currency: "USD",
            offer_payment_method: "Cash",
            title: "1",
            accept_payment_method: "Cash",
            poster: null,
            status : null,
            createdAt: null
        },
        hasBids: false,
        bid: null,
        loading:{
            bid: false,
            post: false,
            bidAction: false
        },
        ratedOffer: false
    };

    constructor(props:any){
        super(props);
        this.loadPost = this.loadPost.bind(this);
        this.state.postData = {
            accept_ammount: 1,
            accept_currency: "BSF",
            description: "1",
            offer_ammount: 1,
            offer_currency: "USD",
            offer_payment_method: "Cash",
            title: "1",
            accept_payment_method: "Cash",
            poster: null,
            status : null,
            createdAt: null
        };
        this.placeBid = this.placeBid.bind(this);
        this.cancelBid = this.cancelBid.bind(this);
        this.setLoading = this.setLoading.bind(this);
    }


    get exchangeRate(){
        let state:any = this.state;
        let offeringAmmount = state.postData['offer_ammount'] || 1;
        let acceptingAmmount = state.postData['accept_ammount'] || 1;
        return offeringAmmount / acceptingAmmount;
    }

    get postId(){
        return this.props.match.params.postId;
    }

    async loadPost(){
        this.setLoading("post", true);
        try{
            let result = await Promise.all([
                await OfferManager.fetchOffer(this.postId),
                await RateManager.hasRateOnOffer(this.postId)
            ]);
            let data = result[0];
            let ratedOffer = result[1];
            if(!data){
                throw new Error("Offer not found");
            }
            this.setState(function(oldState:any){
                oldState['postData'] = data;
                oldState['ratedOffer'] = ratedOffer;
                return oldState;
            });
        }catch(e){
            console.log({e});
            window.alert("Error loading offer");
        }
        this.setLoading("post", false);
    }

    async loadBid(){
        this.setLoading("bid", true);
        try{
            let bids = await BidManager.getBids(this.postId);
            if(bids.size > 0){
                this.setState({
                    bid: bids.docs[0].data()
                });
            }
            if(this.isOwn){
                let hasBids = await BidManager.hasGeneralBids(this.postId);
                this.setState({
                    hasBids
                });
            }
        }catch(err){
            console.log({err});
        }
        this.setLoading("bid", false);
    }

    componentDidMount(){
        if(this.postId){
            this.loadPost()
            .then(()=>{
                this.loadBid();
            });
        }
    }

    setLoading(key:string, value:boolean){
        this.setState(function(prev:any){
            prev.loading[key] = value;
            return prev;
        });
    }

    async placeBid(){
        this.setLoading("bidAction", true);
        let bidData:IBidCreationPayload = {
            poster: null,
            status: null,
            offer: this.postId,    //Offer ID
            createdAt: null
        };
        try{
            await BidManager.createBid(bidData)
            await this.loadBid();
        }catch(e){
            console.log(e);
            window.alert("Error found creating bid.");
        }   
        this.setLoading("bidAction", false);
    }

    async cancelBid(){
        this.setLoading("bidAction", true);
        try{
            await BidManager.cancelBid(this.postId)
            await this.loadBid();
        }catch(e){
            console.log({e});
            window.alert("Cancelling bid");
        }
        this.setLoading("bidAction", false);
    }

    get isOwn(){
        return Firebase.auth.currentUser && this.state.postData.poster && Firebase.auth.currentUser.uid === this.state.postData.poster;
    }

    get canEdit(){
        return this.isOwn && this.state.postData.status === OFFER_STATUS.ACTIVE && !this.isLoading;
    }

    get isLoading(){
        return this.state.loading.bid || this.state.loading.bidAction || this.state.loading.post;
    }

    rateOffer(){
        UIkit.modal(document.getElementById('rateSnippet')).show();
    }

    render(){
        return (
            <div>
                <h3>Oferta. &nbsp;
                    {
                        this.state.postData.status !== null ? 
                        <b>
                            {OfferManager.getOfferStatus(this.state.postData.status || OFFER_STATUS.ACTIVE)}
                        </b>
                        : <></>
                    }
                </h3>
                <fieldset className="uk-fieldset">

                    <div className="uk-margin">
                        <h4>{this.state.postData.title}</h4>
                    </div>

                    <div className="uk-margin">
                        {this.state.postData.description}
                    </div>

                    <div className="uk-margin">
                        <legend className="uk-legend" custom-for="form-stacked-select">Oferta: {UtilsManager.getCurrency(this.state.postData.offer_ammount)} {this.state.postData.offer_currency}</legend>
                    </div>

                    <div className="uk-margin">
                        <legend className="uk-legend" custom-for="form-stacked-select">Paga via: {this.state.postData.offer_payment_method}</legend>
                    </div>

                    <div className="uk-margin">
                        <legend className="uk-legend" custom-for="form-stacked-select">Acepta: {UtilsManager.getCurrency(this.state.postData.accept_ammount)} {this.state.postData.accept_currency}</legend>
                    </div>

                    <div className="uk-margin">
                        <legend className="uk-legend" custom-for="form-stacked-select">Acepta Pago Via: {this.state.postData.accept_payment_method}</legend>
                    </div>

                    <div className="uk-margin">
                        <legend className="uk-legend" style={{fontSize:'1.2em'}}>Tasa de Cambio: <b>{this.exchangeRate}</b></legend>
                    </div>

                    {
                        !this.isLoading && !this.isOwn && !this.state.bid ? 
                        <button className="uk-button uk-button-primary" onClick={this.placeBid}>Colocar Puja</button>
                        : <></>
                    }

                    <div className="uk-position-relative uk-visible-toggle uk-light" uk-slider={""}>
                        <ul className="uk-slider-items uk-child-width-1-2 uk-child-width-1-3@s uk-child-width-1-4@m">
                            <li style={{width:'0px'}}></li>
                            {
                                this.state.postData && this.state.postData.images ? 
                                this.state.postData.images.map((element:IImage) => 
                                    <li key={element.id}>
                                        <img src={element.downloadURL} style={{width:'300px',maxHeight:'500px'}}/>
                                    </li>
                                ) : <></>
                            }
                        </ul>

                        <a className="uk-position-center-left uk-position-small uk-hidden-hover" uk-slidenav-previous={""} uk-slider-item={"previous"}></a>
                        <a className="uk-position-center-right uk-position-small uk-hidden-hover" uk-slidenav-next={""} uk-slider-item={"next"}></a>
                    </div>
                    <hr/>
                    {
                        !this.isOwn && this.state.bid ? 
                        <>
                            {
                                this.state.bid.status === BID_STATUS.ACTIVE ? 
                                <button className="uk-button uk-button-primary" onClick={this.cancelBid}>Cancelar Puja</button>
                                : this.state.ratedOffer ? <>
                                </>
                                :<>
                                    <button id="newRating" className="uk-button uk-button-primary" onClick={this.rateOffer}>Calificar Oferta</button>
                                </>
                            }
                        </>
                        : <></>
                    }
                    
                    {
                        this.canEdit ? 
                        <Link to={`/edit/post/${this.postId}`}>
                            <button className="uk-button uk-button-primary">Editar Oferta</button>
                        </Link>
                        : 
                        <></>
                    }

                    <hr/>
                    {
                        this.isOwn ? 
                        <BidListing match={this.props.match}></BidListing>
                        : <></>
                    }
                    <RateSnippet modalId={"rateSnippet"} offerId={this.postId} />
                    <RateListing match={this.props.match}/>
                </fieldset>
            </div>
        );
    }
}