import React from 'react';
import {Link} from 'react-router-dom';
import { ROUTES } from '../../constants/routes';
import Firebase from '../../lib/Firebase';


export default class Navigation extends React.Component {

    state: {
        loading: boolean
    } = {
        loading: false
    }


    get username(){
        return Firebase.auth.currentUser ? Firebase.auth.currentUser.displayName || Firebase.auth.currentUser.email : "NOT LOGGED IN";
    }

    get profile(){
        return Firebase.auth.currentUser;
    }

    set loading(loading:boolean){
        this.setState({
            loading
        });
    }

    get loading(){
        return this.state.loading;
    }

    async load(){
        this.loading = true;
        await Firebase.waitForAuth();
        this.loading = false;
    }

    componentDidMount(){
        this.load()
        .then(function(){

        })
        .catch(function(){

        });
    }


    render(){
        return (
            <div>
                {
                    !this.loading ?
                    <nav className="uk-navbar-container" uk-navbar="mode: click">
                        <div className="uk-navbar-left">

                            <ul className="uk-navbar-nav">
                                <li>
                                    <Link to={ROUTES.HOME}>Inicio</Link>
                                </li>
                                {
                                    this.profile ? 
                                    <>
                                        <li>
                                            <Link to={ROUTES.NEW_POST}>Nueva Oferta</Link>
                                        </li>
                                        <li>
                                            <Link to={ROUTES.PROFILE}>Perfil</Link>
                                        </li>
                                    </> : <></>
                                }
                                <li>
                                    <a href="#">Autorizacin</a>
                                    <div className="uk-navbar-dropdown">
                                        <ul className="uk-nav uk-navbar-dropdown-nav">
                                            {
                                                this.profile ? 
                                                <li style={{cursor:'pointer'}} onClick={()=>Firebase.auth.signOut()}>
                                                    <a>Salir</a>
                                                </li> :
                                                <>
                                                    <li>
                                                        <Link to={ROUTES.SIGN_UP}>Crear Cuenta</Link>
                                                    </li>
                                                    <li>
                                                        <Link to={ROUTES.SIGN_IN}>Log In</Link>
                                                    </li>
                                                </>
                                            }
                                        </ul>
                                    </div>
                                </li>
                            </ul>

                        </div>
                    </nav>
                    :<></>
                }
            </div>
        )
    }
}