export class Firebase {
	auth: firebase.auth.Auth;
	firestore: firebase.firestore.Firestore;
	storage: firebase.storage.Storage;

	private static _instance: Firebase;

	private constructor() {
		app.initializeApp(config);
		this.auth = app.auth();
		this.firestore = app.firestore();
		this.storage = app.storage();
	}

	static get instance() {
		this._instance = this._instance ? this._instance : new Firebase();
		return this._instance;
	}

	createUserWithEmailAndPassword(email: string, password: string) {
		return this.auth.createUserWithEmailAndPassword(email, password);
	}

	signInWithEmailAndPassword(email: string, password: string) {
		return this.auth.signInWithEmailAndPassword(email, password);
	}

	/**
	 * @description Server Timestamp
	 */
	get firestoreTimestamp(){
		return firebase.firestore.FieldValue.serverTimestamp();
	}

	signOut() {
		this.auth.signOut();
	}

	listenAuthChanges(element:(user:object | null)=>any){
		this.auth.onAuthStateChanged(element);
	}

	async waitForAuth(){
		await new Promise((accept)=>{
			let unsubscribe = this.auth.onAuthStateChanged(function(user){
				accept(user);
				unsubscribe();
			});
		});
	}
}

export default Firebase.instance;