import React, { SyntheticEvent } from 'react';
import ContactManager from '../../lib/Contact';
import Firebase from '../../lib/Firebase';

export default class ProfileComponent extends React.Component {

	state: any = {
		formValue: {}
	};

	constructor(props: any) {
		super(props);
		this.setValue = this.setValue.bind(this);
		this.submit = this.submit.bind(this);
		this.setLoading = this.setLoading.bind(this);
	}

	setLoading(isLoading:boolean){
		this.setState({
			isLoading
		});
	}


	setValue(event: any) {
		const id = (event.currentTarget).getAttribute('custom-id');
		const value = event.currentTarget['value'];
		this.setState(function (state: any) {
			state.formValue[id] = value;
			return state;
		});
	}

	submit(event: SyntheticEvent) {
		event.preventDefault();
		console.log({ form: this.state.formValue });
		this.setLoading(true);
		ContactManager.updateContactInformation(this.state.formValue)
			.then((data) => {
				window.alert("Contacto almacenado");
				this.setLoading(!true);
			})
			.catch( (err) => {
				window.alert("Error actualizando tu contacto");
				this.setLoading(!true);
			});
	}

	get exchangeRate() {
		let offeringAmmount = this.state.formValue['offer_ammount'] || 1;
		let acceptingAmmount = this.state.formValue['accept_ammount'] || 1;
		return offeringAmmount / acceptingAmmount;
	}

	componentDidMount() {
		this.setLoading(true);
		Firebase.listenAuthChanges((user) => {
			if (user) {
				ContactManager
				.getContactInformation()
				.then((data) => {
					this.setState({
						formValue: data
					});
					this.setLoading(!true);
				})
				.catch((err) => {
					window.alert("Error cargando la informacin de contacto");
					this.setLoading(!true);
				});
			}
		})
	}

	render() {
		return (
			<div>
				<form style={{ padding: '2em' }} onSubmit={this.submit}>
					<h3>Informacin de Contacto</h3>
					<fieldset className="uk-fieldset">
						<div className="uk-margin">
							<label className="uk-form-label" custom-for="form-stacked-select">Telfono de Contacto</label>
							<div className="uk-form-controls">
								<input onChange={this.setValue} custom-id="phone" value={this.state.formValue['phone']} type="phone" className="uk-input"></input>
							</div>
						</div>
						<div className="uk-margin">
							<label className="uk-form-label" custom-for="form-stacked-select">Nmero de Contacto</label>
							<div className="uk-form-controls">
								<input onChange={this.setValue} custom-id="whatsapp" value={this.state.formValue['whatsapp']} type="phone" className="uk-input"></input>
							</div>
						</div>
						<div className="uk-margin">
							<label className="uk-form-label" custom-for="form-stacked-select">Email</label>
							<div className="uk-form-controls">
								<input onChange={this.setValue} custom-id="email" value={this.state.formValue['email']} type="email" className="uk-input"></input>
							</div>
						</div>

						<button className="uk-button uk-button-primary" disabled={this.state.isLoading} role="submit">Guardar Informacin de Contacto</button>
					</fieldset>
				</form>

			</div>
		);
	}
}