import React from 'react';
import {IBidCreationPayload} from '../../lib/interface/Bid';
import BidManager from '../../lib/Bid';
import { BID_STATUS, OFFER_STATUS } from '../../constants/status';
import OfferManager from '../../lib/Offer';
import UIKitManager from '../../lib/UIKit';
import RateSnippet from '../RateSnippet';
declare const UIkit:any;

export default class BidListing extends React.Component<{match:{params:{postId:string}}}> {

    state:{
        bids:IBidCreationPayload[],
        loading: boolean,
        displayBid:any,
        displayBidContact: any,
        post: any,
        editingBidRate: string,
        bidPaging: {
            startAfter:IBidCreationPayload | null,
            stopBefore:IBidCreationPayload | null,
            limit: number,
            page: number
        }
    } = {
        bids:[],
        loading: true,
        displayBid:{},
        displayBidContact:{},
        post: null,
        editingBidRate: "",
        bidPaging: {
            startAfter: null,
            stopBefore: null,
            limit: 5,
            page: 0
        }
    };

    constructor(props:any){
        super(props);
        this.load = this.load.bind(this);
        this.setLoading = this.setLoading.bind(this);
        this.rejectBid = this.rejectBid.bind(this);
        this.loadBids = this.loadBids.bind(this);
        this.loadPost = this.loadPost.bind(this);
        this.prevBids = this.prevBids.bind(this);
        this.nextBids = this.nextBids.bind(this);
    }

    setLoading(loading:boolean){
        this.setState({loading});
    }

    async acceptBid(bidId:string){
        this.setLoading(true);
        try{
            await BidManager.acceptBid(bidId);
            await this.load();
        }catch(e){
            window.alert("Bid couldn't be accepted");
        }
        this.setLoading(false);
    }

    get bid():IBidCreationPayload{
        return this.state.displayBid;
    }

    get postId(){
        return this.props.match ? this.props.match.params.postId : "";
    }

    async loadBids(){
        let {startAfter, stopBefore, limit} = this.state.bidPaging;
        this.setLoading(true);
        console.log({
            startAfter, stopBefore, limit
        });
        let bids = await BidManager.fetchGeneralBids(this.postId, startAfter?.createdAt, stopBefore?.createdAt, limit);
        this.setState(function(state:any){
            state['bids'] = bids;
            state['bidPaging'].startAfter = bids[0];
            state['bidPaging'].stopBefore = bids[bids.length-1];
            return state;
        });
        this.setLoading(false);
        this.setState({
            bids
        });
    }

    async loadPost(){
        let post = await OfferManager.fetchOffer(this.postId, true);
        this.setState({post});
    }

    async load(){

        //Load latest bids
        try{
            this.setLoading(true);
            await Promise.all([this.loadBids(), this.loadPost()]);
            this.setLoading(false);
        }catch(e){
            console.log({e});
            this.setLoading(false);
            console.log("Error loading bids...");
        }
    }

    componentDidMount(){
        this.load()
        .catch(function(err){
            console.log("Error loading bids");
        });
        let object:any = document.getElementById('modal-details');
        object.setAttribute('uk-modal','')
    }

    async viewBidDetails(bidId:string){
        this.setLoading(true);
        UIKitManager.showModal("modal-details");
        let bid:any = this.state.bids.find(element => element.id === bidId);
        let contact = await BidManager.getBidContact(bid.poster);
        this.setState({
            displayBid: bid,
            displayBidContact: contact
        });
        this.setLoading(false);
    }

    getStatus(element:IBidCreationPayload){
        switch (element.status) {
            case BID_STATUS.ACTIVE:
                return "ACTIVA";
            case BID_STATUS.ACCEPTED:
                return "ACEPTADA";
            case BID_STATUS.CANCELLED:
                return "CANCELADA";
            case BID_STATUS.REJECTED:
                return "RECHAZADA";
            default:
                return element.status;
        }
    }

    async rejectBid(bidId:string){
        this.setLoading(true);
        try {
            await BidManager.rejectBid(bidId);
            await this.load();
        } catch(e) {
            window.alert("Error rejecting bid");
        }
        this.setLoading(false);
    }

    rateBid(bidId:string){
        this.setState({
            editingBidRate: bidId
        });
        UIKitManager.showModal("rateSnippet");
    }

    prevBids(){
        if(this.state.bidPaging.page > 0){
            this.setLoading(true);
            this.setState(function(oldState:any){
                oldState['bidPaging'].stopBefore = oldState['bidPaging'].startAfter;
                oldState['bidPaging'].startAfter = null;
                oldState['bidPaging'].page-=1;
                return oldState;
            }, ()=>{
                this.setLoading(false);
                this.loadBids();
            });
        }
    }

    nextBids(){
        if(this.state.bids.length >= this.state.bidPaging.limit){
            this.setLoading(true);
            this.setState(function(oldState:any){
                oldState['bidPaging'].startAfter = oldState['bidPaging'].stopBefore;
                oldState['bidPaging'].stopBefore = null;
                oldState['bidPaging'].page+=1;
                return oldState;
            }, ()=>{
                this.setLoading(false);
                this.loadBids();
            });
        }
    }

    render(){
        return (
            <div>
                <h3>Pujas</h3>
                <table className="uk-table">
                    <thead>
                        <tr>
                            <th>ID de Puja</th>
                            <th>Acciones de Puja</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.bids.map((element:IBidCreationPayload) => 
                                <tr
                                className={`
                                ${element.status === BID_STATUS.ACCEPTED ? 'accepted ' : ''}
                                ${element.status === BID_STATUS.REJECTED ? 'rejected ' : ''}
                                `}
                                key={element.id}>
                                    <td>{element.id}</td>
                                    <td>
                                        <button className="uk-button" disabled={this.state.loading} onClick={(ev)=>this.viewBidDetails(element.id || "")}>Details</button>
                                        {
                                            this.state.post && this.state.post.status === OFFER_STATUS.ACTIVE ? 
                                                element.status === BID_STATUS.ACTIVE ? 
                                                    <>
                                                        <button className="uk-button" disabled={this.state.loading} onClick={() => this.acceptBid(element.id || "")}>Accept</button>
                                                        <button className="uk-button" disabled={this.state.loading} onClick={() => this.rejectBid(element.id || "")}>Reject</button>
                                                    </>
                                                : <>
                                                    {
                                                        element.status === BID_STATUS.ACCEPTED ? 
                                                        <>
                                                            <button className="uk-button" disabled={this.state.loading} onClick={()=>this.rateBid(element.id || "")}>Rate Bid</button>
                                                        </>:<></>
                                                    }
                                                </>
                                            : <></>
                                        }
                                    </td>
                                </tr>
                            )
                        }
                    </tbody>
                    <tfoot>
                        <tr>
                            <td><button className="uk-button" disabled={this.state.loading} onClick={this.prevBids}>Pgina Anterior</button></td>
                            <td style={{textAlign:'right'}}><button  disabled={this.state.loading} className="uk-button" onClick={this.nextBids}>Prxima Pgina</button></td>
                        </tr>
                    </tfoot>
                </table>

                <div id="modal-details">
                    <div className="uk-modal-dialog uk-modal-body">
                    <h2 className="uk-modal-title">Bid ID: {this.bid.id}</h2>
                        <h3>STATUS <b>{this.getStatus(this.state.displayBid)}</b></h3>
                        <h3>Contact</h3>
                        <b>Whatsapp</b> {this.state.displayBidContact.whatsapp}
                        <hr/>
                        <b>Phone</b> {this.state.displayBidContact.phone}
                        <hr/>
                        <b>Email</b> {this.state.displayBidContact.email}
                        <hr/>
                        <button className="uk-button uk-button-default uk-modal-close" type="button">Close</button>
                    </div>
                </div>
                <RateSnippet modalId={"rateSnippet"} bidId={this.state.editingBidRate} />
            </div>
        )
    }
}