import { IOffer } from "./interface/Offer";
import { IBidCreationPayload } from "./interface/Bid";
import { IContactUpdatePayload } from "./interface/Contact";

export class CheckManager {
    private static _instance:CheckManager;
    private constructor(){}

    public static get Instance(){
        this._instance = this._instance ? this._instance : new CheckManager();
        return this._instance;
    }


    hasValidOfferCreationPayload(payload:IOffer){
        if(isNaN(payload.accept_ammount) || payload.accept_ammount <= 0){
            return false;
        }

        if(isNaN(payload.offer_ammount) || payload.accept_ammount <= 0){
            return false;
        }

        if(!payload.accept_currency || !payload.description || !payload.offer_currency || !payload.offer_payment_method || !payload.title){
            return false;
        }

        if(payload.description.length > 300 || payload.title.length > 30){
            return false;
        }
        return true;
    }


    hasValidBidData(payload:IBidCreationPayload){
        if(!payload.poster){
            return false;
        }
        if(!payload.offer){
            return false;
        }
        return true;
    }

    hasValidContactUData(data:IContactUpdatePayload){
        if(data.email){
            if(data.email.length > 30 || data.email.length < 5){
                return false;
            }
        }
        if(data.whatsapp){
            if(data.whatsapp.length > 10 || data.whatsapp.length < 5){
                return false;
            }
        }
        if(data.phone){
            if(data.phone.length > 10 || data.phone.length < 5){
                return false;
            }
        }
        return true;
    }
}

export default CheckManager.Instance;