import { IContactUpdatePayload } from "./interface/Contact";
import Firebase from "./Firebase";
import CheckManager from "./Checker";

export class ContactManager {

    private static _instance:ContactManager;

    private constructor(){
    }

    public static get Instance(){
        this._instance = this._instance ? this._instance : new ContactManager();
        return this._instance;
    }

    async updateContactInformation(contactUpdatePayload:IContactUpdatePayload){
        //
        if(!CheckManager.hasValidContactUData(contactUpdatePayload)){
            throw new Error("Invalid data");
        }
        if(!Firebase.auth.currentUser){
            throw new Error("User must be logged in");
        }
        await Firebase.firestore.collection('contact').doc(Firebase.auth.currentUser?.uid).set(contactUpdatePayload);
    }

    async getContactInformation():Promise<IContactUpdatePayload>{
        let doc = await Firebase.firestore.collection('contact').doc(Firebase.auth.currentUser?.uid).get();
        if(doc.exists){
            return doc.data() || {};
        }
        return {};
    }
}

export default ContactManager.Instance;