import { IOffer } from "./interface/Offer";
import { CheckManager } from "./Checker";
import Firebase from "./Firebase/firebase";
import { OFFER_STATUS } from "../constants/status";
import { IImage } from "./interface/Media";

export class OfferManager{

    private static _instance: OfferManager;

    private constructor(){

    }

    public static get Instance(){
        this._instance = this._instance ? this._instance : new OfferManager();
        return this._instance;
    }


    async createOffer(payload: IOffer){
        if(!CheckManager.Instance.hasValidOfferCreationPayload(payload)){
            throw new Error("Invalid data");
        }

        if(!Firebase.auth.currentUser){
            throw new Error("In order to create an offer, the user must be logged-in");
        }

        payload.poster = Firebase.auth.currentUser.uid;
        payload.status = OFFER_STATUS.ACTIVE;
        payload.createdAt = Firebase.firestoreTimestamp;
        
        let resultingID = Firebase.firestore.collection('offers').doc().id;
        await Firebase.firestore.collection('offers').doc(resultingID).set(payload);
        return resultingID;
    }

    async updateOffer(payload: IOffer, offerId:string){
        payload = JSON.parse(JSON.stringify(payload));
        if(!CheckManager.Instance.hasValidOfferCreationPayload(payload)){
            throw new Error("Invalid data");
        }

        if(!Firebase.auth.currentUser){
            throw new Error("In order to create an offer, the user must be logged-in");
        }

        payload.poster = Firebase.auth.currentUser.uid;

        //Note: Can't update status.
        if(payload.images){ //Images go separately
            delete payload.images;
        }
        if(payload.status){ //We don't update status
            delete payload.status;
        }
        if(payload.createdAt){ //We don't update status
            delete payload.createdAt;
        }
        
        await Firebase.firestore.collection('offers').doc(offerId).update(payload);
    }

    async fetchOffer(offerId:string, avoidImages?:boolean){
        let result = await Firebase.firestore.collection('offers').doc(offerId).get();
        let returnData = result.exists ? result.data() : null;
        if(returnData && !avoidImages){
            returnData.images = await this.getImages(offerId);
        }
        return returnData;
    }

    async fetchOffers(includeImages?:boolean, startAfter?:Date, stopBefore?:Date, limit?:number){
        let query = Firebase.firestore.collection('offers')
        .where("status", "==", OFFER_STATUS.ACTIVE);
        query = <any>query.orderBy('createdAt', 'desc');
        if(startAfter){
            query = <any>query.startAfter(startAfter);
            if(limit){
                query = <any>query.limit(limit);
            }
        }else if(stopBefore){
            query = <any>query.endBefore(stopBefore);
            if(limit){
                query = <any>query.limitToLast(limit);
            }
        }else{
            if(limit){
                query = <any>query.limit(limit);
            }
        }
        let offers = await query.get();
        let offersToReturn:IOffer[] = [];
        for(let offerI = 0; offerI < offers.docs.length; offerI++){
            let offerEl = offers.docs[offerI];
            let offer:IOffer = <IOffer>offerEl.data();
            offer.createdAt = offer.createdAt ? offer.createdAt.toDate() : null;
            offer.id = offerEl.id;
            if(includeImages){
                offer.images = await this.getImages(offer.id);
            }
            offersToReturn.push(offer);
        }
        return offersToReturn;
    }

    async getImages(offerId:string){
        let images = await Firebase.firestore.collection(`offers/${offerId}/image`).get();
        let imageFiles:IImage[] = [];
        images.docs.forEach((data)=>{
            let imgData = <any>data.data();
            imgData.id = data.id;
            imageFiles.push(imgData);
        })
        return imageFiles;
    }

    async removeImage(offerId:string, image:IImage){
        if(!offerId || !image){
            throw new Error("Offer and Image ID's must be defined");
        }
        await Firebase.storage.ref(image.ref).delete();
        await Firebase.firestore.doc(`offers/${offerId}/image/${image.id}`).delete();
    }

    async uploadImage(offerId:string, imageFile:File){
        let ext = imageFile.name.split(".").pop();
        let allowedExt = ["jpg", "png"];
        if(allowedExt.indexOf(ext || "") < 0){
            throw new Error("Invalid file image");
        }
        let imageDoc = Firebase.firestore.collection(`offers/${offerId}/image`).doc();
        let filePath = `offers/${offerId}/image/${imageDoc.id}.${ext}`;
        let imageRef = await Firebase.storage.ref(filePath).put(imageFile);
        let downloadUrl = await imageRef.ref.getDownloadURL();
        await imageDoc.set({
            downloadURL: downloadUrl,
            bytes: imageRef.totalBytes,
            ref: filePath
        });
        return imageDoc;
    }

    private async _updateOfferStatus(offerId:string, offerStatus:OFFER_STATUS){
        await Firebase.firestore.collection("offers").doc(offerId).update({
            status: offerStatus
        });
    }

    async closeOffer(offerId:string){
        let offer:IOffer = <any>await this.fetchOffer(offerId, true);
        if(offer.status === OFFER_STATUS.ACTIVE){
            await this._updateOfferStatus(offerId, OFFER_STATUS.CLOSED);
        }else{
            throw new Error("Only active-status offers can be updated");
        }
    }

    async getOffer(offerId:string){
        const offerDoc = await Firebase.firestore.collection('offers').doc(offerId).get();
        return <IOffer>{...offerDoc.data(), id: offerDoc.id};
    }

    getOfferStatus(offerStatus:OFFER_STATUS){

        if(!offerStatus){
            return "";
        }
        switch (offerStatus) {
            case OFFER_STATUS.ACTIVE:
                return "ACTIVE";
            case OFFER_STATUS.CLOSED:
                return "CLOSED";
            case OFFER_STATUS.TAKEN:
                return "TAKEN";
            default:
                return `UNKNOWN (${offerStatus})`;
        }
    }

    testAssumption(){
        console.log((<any>Firebase.firestore).fieldValue);
    }
}

export default OfferManager.Instance;