import React, { SyntheticEvent } from 'react';
import OfferManager from '../../lib/Offer';
import { IOffer } from '../../lib/interface/Offer';
import Firebase from '../../lib/Firebase';
import { Redirect } from 'react-router-dom';

export default class NewPostComponent extends React.Component {

    state:any = {
        formValue:{},
        redirectHome: false
    };

    constructor(props:any){
        super(props);
        this.setValue = this.setValue.bind(this);
        this.submit = this.submit.bind(this);
    }


    setValue(event:any){
        const id = (event.currentTarget).getAttribute('custom-id');
        const value = event.currentTarget['value'];
        this.setState(function(state:any){
            state.formValue[id] = value;
            return state;
        });
    }

    submit(event:SyntheticEvent){
        event.preventDefault();
        OfferManager.createOffer(this.state.formValue)
        .then((resultingId)=>{
            console.log("Created offer ID:",resultingId);
            window.alert("Se ha creado su oferta de manera satisfactoria!");
            this.setState({
                redirectHome: true
            });
        })
        .catch(function(err){
            console.log("Error creating offer: ",err);
        })
    }

    get exchangeRate(){
        let offeringAmmount = this.state.formValue['offer_ammount'] || 1;
        let acceptingAmmount = this.state.formValue['accept_ammount'] || 1;
        return offeringAmmount / acceptingAmmount;
    }

    componentDidMount(){
        /**
         *      
        */
    }

    render(){
        if(this.state.redirectHome){
            return <Redirect to="/"/>
        }
        return (
            <div>
                <form style={{padding:'2em'}} onSubmit={this.submit}>
                    <h3>Nueva Oferta</h3>
                    <fieldset className="uk-fieldset">

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Ttulo</label>
                            <div className="uk-form-controls">
                                <input onChange={this.setValue} custom-id='title' className="uk-input" type="text" placeholder="Title"/>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Descripcin de La Oferta</label>
                            <div className="uk-form-controls">
                                <textarea onChange={this.setValue} custom-id='description' className="uk-textarea" maxLength={300}></textarea>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Moneda de Oferta</label>
                            <div className="uk-form-controls">
                                <select className="uk-select" id="form-stacked-select" onChange={this.setValue} custom-id='offer_currency'>
                                    <option>Selecciona tu Moneda</option>
                                    <option value="USD">USD</option>
                                    <option value="BSF">BSF</option>
                                </select>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Cantidad de Oferta</label>
                            <div className="uk-form-controls">
                                <input onChange={this.setValue} custom-id="offer_ammount" type="number" className="uk-input"></input>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Mtodo de Pago Aceptado</label>
                            <div className="uk-form-controls">
                                <select onChange={this.setValue} custom-id="offer_payment_method" className="uk-select" id="form-stacked-select">
                                    <option>Selecciona el Mtodo de Pago para tu oferta</option>
                                    <option value="transferencia">Transferencia</option>
                                    <option value="efectivo">Efectivo</option>
                                </select>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Moneda Aceptada como Pago</label>
                            <div className="uk-form-controls">
                                <select onChange={this.setValue} custom-id="accept_currency" className="uk-select" id="form-stacked-select">
                                    <option>Selecciona tu Moneda</option>
                                    <option value="USD">USD</option>
                                    <option value="BSF">BSF</option>
                                </select>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Cantidad de Moneda Aceptada</label>
                            <div className="uk-form-controls">
                                <input onChange={this.setValue} custom-id="accept_ammount" type="number" className="uk-input"></input>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <label className="uk-form-label" custom-for="form-stacked-select">Mtodo de Pago Aceptado</label>
                            <div className="uk-form-controls">
                                <select onChange={this.setValue} custom-id="accept_payment_method" className="uk-select" id="form-stacked-select">
                                    <option>Selecciona tu Mtodo de Pago Aceptado</option>
                                    <option value="transferencia">Transferencia</option>
                                    <option value="efectivo">Efectivo</option>
                                </select>
                            </div>
                        </div>

                        <div className="uk-margin">
                            <legend className="uk-legend" style={{fontSize:'1.2em'}}>Tasa de Cambio: <b>{this.exchangeRate}</b></legend>
                        </div>

                        <button className="uk-button uk-button-primary" role="submit">Crear Nueva Oferta</button>
                    </fieldset>
                </form>

            </div>
        );
    }
}