import React from 'react';
import Firebase from '../../lib/Firebase';
import RateManager from '../../lib/Rate';
import RateListing from '../RateListing';

export default class PublicProfileComponent extends React.Component {

	state: {
		formValue: {},
        bidPaging: {
            startAfter: Date | null,
            stopBefore: Date | null,
            limit: number,
            page: number
		},
		ratePaging: {
            startAfter: Date | null,
            stopBefore: Date | null,
            limit: number,
            page: number
		}
	} = {
		formValue: {},
        bidPaging: {
            startAfter: null,
            stopBefore: null,
            limit: 5,
            page: 0
        },
        ratePaging: {
            startAfter: null,
            stopBefore: null,
            limit: 5,
            page: 0
        }
	};

	constructor(props: any) {
		super(props);
		this.setLoading = this.setLoading.bind(this);
	}

	setLoading(isLoading:boolean){
		this.setState({
			isLoading
		});
	}

	get profileId(){
		let anyProps:any = this.props;
		let profileId = anyProps['match']['params']['profileId'];
		return profileId;
	}


	componentDidMount() {
	}

	render() {
		return (
			<div>
				<h3>User</h3>
				<h4>Bid Ratings</h4>
				<RateListing match={{params:{ratedUser:this.profileId, ratingType:'bid'}}}/>
				<h4>Offer Ratings</h4>
				<RateListing match={{params:{ratedUser:this.profileId, ratingType:'offer'}}}/>
			</div>
		);
	}
}