import React, { SyntheticEvent } from 'react';
import FirebaseInstance, { Firebase } from '../../lib/Firebase/firebase';

export class SignInComponent extends React.Component<{firebase:Firebase}> {

    state:any = {
        formValue:{}
    };

    constructor(props:any){
        super(props);
        this.setValue = this.setValue.bind(this);
        this.submit = this.submit.bind(this);
    }


    setValue(event:any){
        const id = (event.currentTarget).getAttribute('custom-id');
        const value = event.currentTarget['value'];
        this.setState(function(state:any){
            state.formValue[id] = value;
            return state;
        });
    }

    submit(event:SyntheticEvent){
        event.preventDefault();
        FirebaseInstance.signInWithEmailAndPassword(this.state.formValue['email'], this.state.formValue['password'])
        .then(function(data){
            console.log("Data");
        })
        .catch(function(err){
            window.alert("Error");
        })
    }

    componentDidMount(){
        
    }

    render(){
        return (
            <div>
                <form style={{padding:'2em'}} onSubmit={this.submit}>
                    <h3>Sign In</h3>
                    <fieldset className="uk-fieldset">

                        <legend className="uk-legend" style={{fontSize:'1.2em'}}>Email</legend>

                        <div className="uk-margin">
                            <input onChange={this.setValue} custom-id='email' className="uk-input" type="email" placeholder="Email"/>
                        </div>

                        <legend className="uk-legend" style={{fontSize:'1.2em'}}>Password</legend>

                        <div className="uk-margin">
                            <input onChange={this.setValue} custom-id='password' className="uk-input" type="password" placeholder="Password"/>
                        </div>

                        <button className="uk-button uk-button-primary" role="submit">Sign In</button>

                    </fieldset>
                </form>

            </div>
        );
    }
}