declare const UIkit:any;
export class UIKitManager {
    private static _instance: UIKitManager;
    public static get Instance(){
        this._instance = this._instance ? this._instance : new UIKitManager();
        return this._instance;
    }
    private constructor(){

    }

    showModal(modalId:string, options?:any){
        UIkit.modal(document.getElementById(modalId)).show(options ? options : {});
    }

    hideModal(modalId:string, options?:any){
        UIkit.modal(document.getElementById(modalId)).hide(options ? options : {});
    }

    
}

export default UIKitManager.Instance;