import numeral from 'numeral';

export class UtilsManager {
    private static _instance: UtilsManager;
    private constructor(){}
    static get Instance(){
        this._instance = this._instance ? this._instance : new UtilsManager();
        return this._instance;
    }


    getDate(date:Date){
        return date ? date.toLocaleString() : null;
    }

    getCurrency(ammount:number){
        return numeral(ammount).format("0,0")
    }
}

export default UtilsManager.Instance;