import React from 'react';
import Navigation from '../Navigation';
import {
  BrowserRouter as Router,
  Route,
  Redirect,
} from 'react-router-dom';
import { ROUTES } from '../../constants/routes';
import HomeComponent from '../Home';
import LandingComponent from '../Landing';
import NewPostComponent from '../NewPost'
import { SignUpComponent } from '../SignUp';
import {SignInComponent} from '../SignIn'
import {Helmet} from 'react-helmet'
import Firebase from '../../lib/Firebase';
import {ViewOfferComponent, ViewOwnOfferComponent} from '../Post'
import ProfileComponent from '../Profile';
import PublicProfileComponent from '../PublicProfile';


export class BaseComponent extends React.Component {

    state:{
        currentUser:any
    } = {
        currentUser:null
    }

    componentDidMount(){
        Firebase.auth.onAuthStateChanged((data)=>{
            this.setState(function(oldie:any){
                oldie['currentUser'] = data;
                return oldie;
            });
        });
    }


    render(){
        return (
            <Router>
                <Helmet>
                    <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/uikit@3.5.4/dist/css/uikit.min.css" />
                    <script src="https://cdn.jsdelivr.net/npm/uikit@3.5.4/dist/js/uikit.min.js"></script>
                    <script src="https://cdn.jsdelivr.net/npm/uikit@3.5.4/dist/js/uikit-icons.min.js"></script>
                </Helmet>
                <Navigation/>
                <hr/>
                <div id="content">
                    <Route exact path={ROUTES.SIGN_UP} component={SignUpComponent}/>
                    <Route exact path={ROUTES.HOME} component={HomeComponent}/>
                    <Route exact path={ROUTES.SIGN_IN} component={SignInComponent}/>
                    <Route exact path={ROUTES.NEW_POST} component={NewPostComponent}/>
                    <Route path={ROUTES.VIEW_POST} component={ViewOfferComponent}/>
                    <Route path={ROUTES.EDIT_POST} component={ViewOwnOfferComponent}/>
                    <Route exact path={ROUTES.PROFILE} component={ProfileComponent}/>
                    <Route path={ROUTES.PUBLIC_PROFILE} component={PublicProfileComponent}/>
                </div>
            </Router>
        );
    }
}