import React from 'react';
import { IOffer } from '../../lib/interface/Offer';
import { Link } from 'react-router-dom';
import { ROUTES } from '../../constants/routes';
import OfferManager from '../../lib/Offer';
import UtilsManager from '../../lib/Utils';


export default class HomeComponent extends React.Component {

    state:{
        offers:IOffer[],
        offersPaging: {
            startAfter:IOffer | null,
            stopBefore:IOffer | null,
            limit: number,
            page: number
        }
    } = {
        offers:[],
        offersPaging: {
            startAfter:null,
            stopBefore:null,
            limit: 5,
            page: 0
        }
    };

    constructor(props:any){
        super(props);
        this.loadOffers = this.loadOffers.bind(this);
        this.pageBack = this.pageBack.bind(this);
        this.pageForth = this.pageForth.bind(this);
    }

    async loadOffers(){
        try{
            let {startAfter, stopBefore, limit} = this.state.offersPaging;
            console.log({
                startAfter: startAfter?.createdAt, stopBefore: stopBefore?.createdAt, limit,
                page: this.state.offersPaging.page
            });
            let offers = await OfferManager.fetchOffers(false, startAfter?.createdAt, stopBefore?.createdAt, limit);
            this.setState(function(state:any){
                state['offers'] = offers;
                state['offersPaging'].startAfter = offers[0];
                state['offersPaging'].stopBefore = offers[offers.length-1];
                return state;
            });
        }catch(e){
            console.log({e});
            console.log("Error loading offers...");
        }
    }

    componentDidMount(){
        this.loadOffers();
    }

    pageBack(){
        if(this.state.offersPaging.page > 0){
            this.setState(function(oldState:any){
                oldState['offersPaging'].stopBefore = oldState['offersPaging'].startAfter;
                oldState['offersPaging'].startAfter = null;
                oldState['offersPaging'].page-=1;
                return oldState;
            }, ()=>{
                this.loadOffers();
            });
        }
    }

    pageForth(){
        if(this.state.offers.length >= this.state.offersPaging.limit){
            this.setState(function(oldState:any){
                oldState['offersPaging'].startAfter = oldState['offersPaging'].stopBefore;
                oldState['offersPaging'].stopBefore = null;
                oldState['offersPaging'].page+=1;
                return oldState;
            }, ()=>{
                this.loadOffers();
            });
        }
    }

    render(){
        return (
            <div>
                <h3>Inicio</h3>
                <table className="uk-table">
                    <thead>
                        <tr>
                            <th>Fecha</th>
                            <th>Ofrece</th>
                            <th>Ofrece Pago</th>
                            <th>Acepta</th>
                            <th>Acepta Pago</th>
                            <th>Acciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.offers.map((element:IOffer) => 
                                <tr key={element.id}>
                                    <td>{UtilsManager.getDate(element.createdAt)}</td>
                                    <td>{UtilsManager.getCurrency(element.offer_ammount)}{element.offer_currency}</td>
                                    <td>{element.offer_payment_method}</td>
                                    <td>{UtilsManager.getCurrency(element.accept_ammount)}{element.accept_currency}</td>
                                    <td>{element.accept_payment_method}</td>
                                    <td>
                                        <Link to={ROUTES.VIEW_POST.replace(":postId", element.id+"")}>
                                            <button className="uk-button uk-button-default">Info</button>
                                        </Link>
                                        <Link to={ROUTES.PUBLIC_PROFILE.replace(':profileId', element.poster+"")}>
                                            <button className="uk-button uk-button-default">Poster</button>
                                        </Link>
                                    </td>
                                </tr>
                            )
                        }
                    </tbody>
                    <tfoot>
                        <tr>
                            <td colSpan={3} style={{
                                textAlign:'left'
                            }}>
                                <button className='uk-button' onClick={this.pageBack}>Atrs</button>
                            </td>
                            <td colSpan={2} style={{
                                textAlign:'right'
                            }}>
                                <button className='uk-button' onClick={this.pageForth}>Siguiente</button>
                            </td>
                        </tr>
                    </tfoot>
                </table>
                
            </div>
        )
    }
}