import React, { SyntheticEvent } from 'react';
import RateManager from '../../lib/Rate';
import UIKitManager from '../../lib/UIKit';

declare const UIkit:any;
export default class RateSnippet extends React.Component<{modalId:string, bidId?:string, offerId?:string}> {

    state:{
        formValue:{
            rate: string,
            comment: string
        },
        loading:any
    } = {
        formValue:{
            rate: "0",
            comment: ""
        },
        loading: {
            saving: false
        }
    };

    constructor(props:any){
        super(props);
        this.load = this.load.bind(this);
        this.save = this.save.bind(this);
        this.setInput = this.setInput.bind(this);
        this.setLoading = this.setLoading.bind(this);
    }

    async load(){

        //Load latest posts
        try{
            this.listenUIKit(()=>{
                let bb:any = window;
                try{
                    bb.UIkit.modal(document.getElementById(this.props.modalId)).hide();
                }catch(e){
                    console.log({e});
                }
            });
        }catch(e){
            console.log({e});
            console.log("Error setting modal...");
        }
    }

    setLoading(key:string, value:boolean){
        this.setState(function(prev:any){
            prev.loading[key] = value;
            return prev;
        });
    }

    listenUIKit(callback:()=>any){
        let interval = setInterval(function(){
            let bb:any = window;
            if(bb.UIkit){
                clearInterval(interval);
                callback();
            }
        }, 50);
    }

    componentDidMount(){
        this.load();
    }

    async save(){
        this.setLoading("saving", true)
        try{
            if(this.props.offerId){
                console.log("Rating offer");
                await RateManager.rateOffer(this.state.formValue.comment, this.state.formValue.rate, this.props.offerId);
                window.alert("Offer rated properly");
                UIKitManager.hideModal(this.props.modalId);
            }else if(this.props.bidId){
                console.log("Rating bid");
                await RateManager.rateBid(this.state.formValue.comment, this.state.formValue.rate, this.props.bidId);
                window.alert("Bid rated properly");
                UIKitManager.hideModal(this.props.modalId);
            }else{
                console.log("Nothing to rate...");
                console.log(this.props);
            }
        }catch(e){
            console.log({e});
            window.alert("Error rating");
        }
        
        this.setLoading("saving", false)
    }

    hide(){
    }

    setInput(event:SyntheticEvent){
        let input:any = event.currentTarget;
        let value = input.value, id = input.id;

        this.setState(function(state:any){
            state.formValue[id] = value;
            return state;
        });
    }

    get isLoading(){
        for(const key of Object.keys(this.state.loading)){
            if(this.state.loading[key]){
                return true;
            }
        }
        return false;
    }

    render(){
        return (
            <div>
                <div id={this.props.modalId}>
                    <div className="uk-modal-dialog uk-modal-body">
                    <h2 className="uk-modal-title">Review your experience</h2>
                        <fieldset className="uk-fieldset">
                            <div className="uk-margin">
                                <label className="uk-form-label" custom-for="form-stacked-select">Your Rating</label>
                                <div className="uk-form-controls">
                                    <select onChange={this.setInput} className="uk-select" id="rate">
                                        <option value="0">0/5</option>
                                        <option value="20">1/5</option>
                                        <option value="40">2/5</option>
                                        <option value="60">3/5</option>
                                        <option value="80">4/5</option>
                                        <option value="100">5/5</option>
                                    </select>
                                </div>
                            </div>
                            <div className="uk-margin">
                                <label className="uk-form-label" custom-for="form-stacked-select">Your Comment</label>
                                <div className="uk-form-controls">
                                    <textarea onChange={this.setInput} id='comment' className="uk-textarea"></textarea>
                                </div>
                            </div>
                        </fieldset>
                        <button disabled={this.isLoading} className="uk-button uk-button-default" type="button" onClick={this.save}>Save</button>
                        <button disabled={this.isLoading} className="uk-button uk-button-default uk-modal-close" type="button">Close</button>
                    </div>
                </div>
            </div>
        )
    }
}