import React from 'react';
import {IBidRating} from '../../lib/interface/Bid';
import { IOfferRating } from '../../lib/interface/Offer';
import RateManager from '../../lib/Rate';
import Firebase from '../../lib/Firebase';

export default class RateListing extends React.Component<{match:{params:{postId?:string, bidId?:string, posterId?:string, ratedUser?:string, ratingType?:'offer'|'bid'}}}> {

    state:{
        ratings: IBidRating[] | IOfferRating[] | any[],
        loading: boolean,
        paging: {
            startAfter:IBidRating | IOfferRating | any,
            stopBefore:IBidRating | IOfferRating | any,
            limit: number,
            page: number
        }
    } = {
        ratings: [],
        loading: false,
        paging: {
            startAfter:null,
            stopBefore:null,
            limit: 5,
            page: 0
        }
    };

    constructor(props:any){
        super(props);
        this.load = this.load.bind(this);
        this.setLoading = this.setLoading.bind(this);
        this.loadRatings = this.loadRatings.bind(this);
        this.nextPage = this.nextPage.bind(this);
        this.prevPage = this.prevPage.bind(this);
    }

    setLoading(loading:boolean){
        this.setState({loading});
    }

    get postId(){
        return this.props.match.params.postId;
    }

    get bidId(){
        return this.props.match.params.bidId;
    }

    get posterId(){
        return this.props.match.params.posterId;
    }

    get ratedUser(){
        return this.props.match.params.ratedUser;
    }

    get ratingType(){
        return this.props.match.params.ratingType || 'offer';
    }

    async load(){
        await Firebase.waitForAuth();
        try{
            this.loadRatings();
        }catch(e){
            console.log({e});
            window.alert("Error loading ratings!");
        }
    }

    async loadRatings(){
        this.setLoading(true);
        try{
            let ratings:IBidRating[] | IOfferRating[] = [];
            if(this.bidId){
                console.log("Loading bid ID");
            }else if(this.postId){
                ratings = await RateManager
                .fetchOfferRatings(this.postId || "",
                    this.state.paging.startAfter?.createdAt,
                    this.state.paging.stopBefore?.createdAt,
                    this.state.paging.limit);
            }else if(this.ratedUser){
                ratings = await RateManager.fetchUserRatings(this.ratedUser, this.ratingType, this.state.paging.startAfter, this.state.paging.stopBefore, this.state.paging.limit);
            }
            this.setState(function(state:any){
                state['ratings'] = ratings;
                state['paging'].startAfter = ratings[0];
                state['paging'].stopBefore = ratings[ratings.length-1];
                return state;
            });
        }catch(e){
            console.log({e});
            window.alert("Error loading the ratings!");
        }
        this.setLoading(false);
    }

    componentDidMount(){
        this.load();
    }

    getRating(rating:IOfferRating | IBidRating){
        return (parseInt(rating.rate+"")/20)+"/5";
    }

    prevPage(){
        if(this.state.paging.page > 0){
            this.setLoading(true);
            this.setState(function(oldState:any){
                oldState['paging'].stopBefore = oldState['paging'].startAfter;
                oldState['paging'].startAfter = null;
                oldState['paging'].page-=1;
                return oldState;
            }, ()=>{
                this.setLoading(false);
                this.loadRatings();
            });
        }
    }

    nextPage(){
        if(this.state.ratings.length >= this.state.paging.limit){
            this.setLoading(true);
            this.setState(function(oldState:any){
                oldState['paging'].startAfter = oldState['paging'].stopBefore;
                oldState['paging'].stopBefore = null;
                oldState['paging'].page+=1;
                return oldState;
            }, ()=>{
                this.setLoading(false);
                this.loadRatings();
            });
        }
    }

    getDate(element:IOfferRating | IBidRating){
        return element.createdAt+"";
    }

    getData(){
        let elements:any[] = this.state.ratings;
        if(elements && elements.length){
            return elements.map(element => (
                <div className="uk-card uk-card-body" key={element.id}
                style={{
                    border: "1px solid grey",
                    borderRadius: "10px",
                    margin: "0.5em"
                }}>
                    <b>{this.getRating(element)}</b> <small>{this.getDate(element)}</small>
                    <br/> 
                    {element.comment}
                </div>
            ));
        }
    }

    render(){
        return (
            <div>
                <table className="uk-table">
                    <tbody>
                        <tr>
                            <td colSpan={2}>
                                <h3>Ratings</h3>
                                {
                                    
                                    this.getData()
                                }
                            </td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td>
                                <button className="uk-button" disabled={this.state.loading} onClick={this.prevPage}>Anterior Pgina</button>
                            </td>
                            <td style={{textAlign:'right'}}>
                                <button className="uk-button"  disabled={this.state.loading} onClick={this.nextPage}>Prxima Pgina</button>
                            </td>
                        </tr>
                    </tfoot>
                </table>
            </div>
        )
    }
}